}

.Dialogs_buttonBack__37pUe:before{
    content:"";
    width:17px;
    height:2px;
    position:absolute;
    bottom:24px;
    left:18px;
    -webkit-transform: rotate(35deg);
            transform: rotate(35deg);
    background-color: #575656;
    color:#575656;
}

.Dialogs_buttonBack__37pUe:after{
    content:"";
    width:17px;
    height:2px;
    position:absolute;
    top:24px;
